@tailwind base;
@tailwind components;
@tailwind utilities;

/* Dark-themed scrollbar (single definition) */
.scrollbar-dark::-webkit-scrollbar {
  width: 8px;
}
.scrollbar-dark::-webkit-scrollbar-track {
  background: #1e293b;
  border-radius: 8px;
}
.scrollbar-dark::-webkit-scrollbar-thumb {
  background-color: #06b6d4;
  border-radius: 8px;
  border: 2px solid #1e293b;
}
.scrollbar-dark::-webkit-scrollbar-thumb:hover {
  background-color: #0ea5e9;
}

/* Floating product animation */
@keyframes float {
  0% { transform: translateY(0px) rotate(0deg); }
  50% { transform: translateY(-20px) rotate(3deg); }
  100% { transform: translateY(0px) rotate(0deg); }
}

/* Floating item style */
.floating-item {
  animation: float 6s ease-in-out infinite;
  transition: transform 0.3s ease;
  opacity: 0.8; /* slightly transparent */
  z-index: 2;   /* above background */
  position: absolute;
}

/* Optional animation delays */
.animation-delay-0 { animation-delay: 0s; }
.animation-delay-1 { animation-delay: 1.5s; }
.animation-delay-2 { animation-delay: 2.5s; }
.animation-delay-3 { animation-delay: 3.5s; }
